<template>
  <div class="view-header-flex-container" id="view-header-flex-container-div">
    <div class="view-header-text-container" id="view-header-text-container">
        <h1 class="view-header" id="view-header-h1">{{ title ? title : "Title goes here"}}</h1>
        <div class="view-header-hr"></div>
        <h2 class="view-header" id="view-header-h2">{{ subtitle ? subtitle : "Subtitle goes here"}}</h2>
    </div>
  </div>
</template>

<script>
export default {
    props: ["title", "subtitle"]
}

window.onscroll = () => { scrollFunction(); };
window.onload = () => { scrollFunction(); setHeaderPosition; }
window.onresize = () => { setHeaderPosition() };

function scrollFunction() {
  if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
    setHeaderPosition()
    document.getElementById("view-header-h1").className = "view-header-scrolled";
    document.getElementById("view-header-h2").className = "view-header-scrolled";
    document.getElementById("view-header-text-container").className = "view-header-text-container-scrolled";
    document.getElementById("view-header-flex-container-div").className = "view-header-flex-container-scrolled";
  }
  else {
    document.getElementById("view-header-h1").className = "view-header";
    document.getElementById("view-header-h2").className = "view-header";

    document.getElementById("view-header-text-container").className = "view-header-text-container";
    document.getElementById("view-header-flex-container-div").className = "view-header-flex-container";
    document.getElementById("view-header-flex-container-div").style.left = 0;

  }
}

function setHeaderPosition() {
  let header_container = document.getElementById("view-header-text-container")
  if (header_container.className === "view-header-text-container-scrolled") {
    const logo_size = document.getElementById("logo-name-div").clientWidth
    const nav_size = document.getElementById("hamnav").clientWidth
    const nav_header_size = document.getElementById("nav-header").offsetWidth
    let new_pos = (document.getElementById("logo-name-div").offsetWidth/2 + (nav_header_size - (logo_size + nav_size)) / 2) + "px"
    document.getElementById("view-header-flex-container-div").style.left = new_pos
    // header_container.style.top = "0vh"
    console.log(new_pos)
  }
}
</script>

